import { css } from 'lit';

export const styles = css`:host(igc-calendar) {
  --content-background: var(--ig-calendar-content-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --content-text-color: var(--ig-calendar-content-text-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --border-color: var(--ig-calendar-border-color, hsla(var(--ig-gray-300), var(--ig-gray-a)));
  --header-background: var(--ig-calendar-header-background, hsla(var(--ig-secondary-500), var(--ig-secondary-a)));
  --header-text-color: var(--ig-calendar-header-text-color, var(--ig-secondary-500-contrast));
  --picker-background-color: var(--ig-calendar-picker-background-color, hsla(var(--ig-gray-100), var(--ig-gray-a)));
  --picker-arrow-color: var(--ig-calendar-picker-arrow-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --picker-arrow-hover-color: var(--ig-calendar-picker-arrow-hover-color, hsla(var(--ig-primary-800), var(--ig-primary-a)));
  --picker-text-color: var(--ig-calendar-picker-text-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --picker-text-hover-color: var(--ig-calendar-picker-text-hover-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --inactive-text-color: var(--ig-calendar-inactive-text-color, hsla(var(--ig-gray-500), var(--ig-gray-a)));
  --label-color: var(--ig-calendar-label-color, hsla(var(--ig-info-500), var(--ig-info-a)));
  --weekend-text-color: var(--ig-calendar-weekend-text-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --year-current-text-color: var(--ig-calendar-year-current-text-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --month-current-text-color: var(--ig-calendar-month-current-text-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --month-hover-current-text-color: var(--ig-calendar-month-hover-current-text-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --year-hover-text-color: var(--ig-calendar-year-hover-text-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --month-hover-background: var(--ig-calendar-month-hover-background, hsla(var(--ig-gray-100), var(--ig-gray-a)));
  --month-hover-text-color: var(--ig-calendar-month-hover-text-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --date-selected-background: var(--ig-calendar-date-selected-background, hsla(var(--ig-primary-300), var(--ig-primary-a)));
  --date-selected-hover-background: var(--ig-calendar-date-selected-hover-background, hsla(var(--ig-gray-100), 0.18));
  --date-selected-focus-background: var(--ig-calendar-date-selected-focus-background, hsla(var(--ig-gray-100), 0.18));
  --date-selected-current-background: var(--ig-calendar-date-selected-current-background, hsla(var(--ig-primary-600), var(--ig-primary-a)));
  --date-selected-current-hover-background: var(--ig-calendar-date-selected-current-hover-background, hsla(var(--ig-primary-700), var(--ig-primary-a)));
  --date-selected-current-focus-background: var(--ig-calendar-date-selected-current-focus-background, hsla(var(--ig-primary-700), var(--ig-primary-a)));
  --date-selected-text-color: var(--ig-calendar-date-selected-text-color, var(--ig-primary-900-contrast));
  --date-selected-hover-foreground: var(--ig-calendar-date-selected-hover-foreground, var(--ig-gray-900-contrast));
  --date-selected-focus-foreground: var(--ig-calendar-date-selected-focus-foreground, var(--ig-gray-900-contrast));
  --date-current-text-color: var(--ig-calendar-date-current-text-color, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --date-current-hover-foreground: var(--ig-calendar-date-current-hover-foreground, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --date-current-focus-foreground: var(--ig-calendar-date-current-focus-foreground, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --date-selected-current-foreground: var(--ig-calendar-date-selected-current-foreground, var(--ig-secondary-500-contrast));
  --date-selected-current-hover-foreground: var(--ig-calendar-date-selected-current-hover-foreground, var(--ig-primary-600-contrast));
  --date-selected-current-focus-foreground: var(--ig-calendar-date-selected-current-focus-foreground, var(--ig-primary-600-contrast));
  --date-current-bg-color: var(--ig-calendar-date-current-bg-color, hsla(var(--ig-primary-600), var(--ig-primary-a)));
  --date-current-hover-background: var(--ig-calendar-date-current-hover-background, hsla(var(--ig-primary-600), var(--ig-primary-a)));
  --date-current-focus-background: var(--ig-calendar-date-current-focus-background, hsla(var(--ig-primary-600), var(--ig-primary-a)));
  --date-hover-background: var(--ig-calendar-date-hover-background, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --date-focus-background: var(--ig-calendar-date-focus-background, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --date-special-background: var(--ig-calendar-date-special-background, hsla(var(--ig-warn-500), var(--ig-warn-a)));
  --date-special-text-color: var(--ig-calendar-date-special-text-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --date-disabled-text-color: var(--ig-calendar-date-disabled-text-color, hsla(var(--ig-gray-400), var(--ig-gray-a)));
  --date-disabled-background: var(--ig-calendar-date-disabled-background, hsla(var(--ig-gray-100), var(--ig-gray-a)));
  --week-number-color: var(--ig-calendar-week-number-color, hsla(var(--ig-gray-500), var(--ig-gray-a)));
  --week-number-background: var(--ig-calendar-week-number-background, hsla(var(--ig-gray-300), 0.5));
  --border-radius: var(--ig-calendar-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.2) * 1.25rem), 1.25rem));
  --date-border-radius: var(--ig-calendar-date-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.2) * 1.25rem), 1.25rem));
  --month-border-radius: var(--ig-calendar-month-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.2) * 1.25rem), 1.25rem));
  background: hsla(var(--ig-surface-500), var(--ig-surface-a));
  color: var(--ig-surface-500-contrast);
  border-radius: var(--border-radius);
  border: 0.0625rem solid hsla(var(--ig-gray-300), var(--ig-gray-a));
}
:host(igc-calendar) igc-days-view::part(date-inner) {
  border-radius: var(--date-border-radius);
}
:host(igc-calendar) igc-months-view::part(month-inner) {
  border-radius: var(--month-border-radius);
}
:host(igc-calendar) igc-years-view::part(year-inner) {
  border-radius: var(--month-border-radius);
}

:host([hide-header]) [part~=navigation] {
  border-start-start-radius: var(--border-radius);
  border-start-end-radius: var(--border-radius);
}

[part~=navigation-button] {
  background: transparent;
  color: hsla(var(--ig-primary-500), var(--ig-primary-a));
}
[part~=navigation-button]:hover, [part~=navigation-button]:focus {
  color: hsla(var(--ig-primary-600), var(--ig-primary-a));
}

[part=months-navigation],
[part=years-navigation] {
  background: transparent;
  color: hsla(var(--ig-gray-800), var(--ig-gray-a));
}
[part=months-navigation]:hover, [part=months-navigation]:focus,
[part=years-navigation]:hover,
[part=years-navigation]:focus {
  color: hsla(var(--ig-secondary-500), var(--ig-secondary-a));
}

[part~=navigation],
[part~=header] {
  background: hsla(var(--ig-secondary-50), 0.2);
  color: var(--ig-gray-50-contrast);
}

[part=header-title] {
  font-family: var(--ig-subtitle-1-font-family, var(--ig-font-family));
  font-size: var(--ig-subtitle-1-font-size);
  font-weight: var(--ig-subtitle-1-font-weight);
  font-style: var(--ig-subtitle-1-font-style);
  line-height: var(--ig-subtitle-1-line-height);
  letter-spacing: var(--ig-subtitle-1-letter-spacing);
  text-transform: var(--ig-subtitle-1-text-transform);
  margin-top: var(--ig-subtitle-1-margin-top);
  margin-bottom: var(--ig-subtitle-1-margin-bottom);
}

[part=header] {
  background: hsla(var(--ig-secondary-50), 0.2);
  color: var(--ig-gray-50-contrast);
}`;
